
:root{
    --background-color-1: #90aba0;
    --text-dark-color:rgb(219, 107, 32);
    --text-trophy-color: rgb(8, 8, 8) 0px 0 30px;
    --bar-light-color: #dfe8e4;
    --h2-font-size: 50px;
    --h1-font-size: 80px;
}

body{
    background-color: var(--background-color-1);
}

header{
    z-index: 1;
    background-image: url(../img/windfarm.jpg);
    background-color: var(--bar-light-color);
    display: flex;
    flex-basis: 100vw;
    flex-direction: row;
    align-content: center;
    align-items: center;
    text-align: center;
    position: sticky;
}

header h1{
    z-index: 1000;
    font-size: var(--h1-font-size);
    flex: 1;
    width: 14%;
    color: var(--text-dark-color);
    text-shadow: rgb(8, 8, 8) 1px 0px 1px;
}

header nav{
    z-index: 1000;
    flex:3;
    width: 86%;
}

#particles-js{
    z-index: 1;
    position: fixed;
    width: 100%;
    height: 150px;
}

header nav ul{
    display: flex;
    list-style: none;
    flex-direction: row;
}

header nav ul li{
    flex: 1;
    padding: 20px;
}

header nav ul li a{
    z-index: 9999;
    box-shadow: var(--text-trophy-color);
    background-color: var(--text-dark-color);
}

header nav ul li a:hover{
    opacity: 50%;
    color: var(--background-color-1);
}



main{
    z-index: 1000;
    background: var(--bar-light-color);
    display: flex;
    flex-direction: row;
    border-top: solid;
    border-color: var(--background-color-1);
    border-width: 8px;
}

.info{
    flex: 5;
    display: flex;
    flex-direction: column;
    border-left: solid;
    border-color: var(--background-color-1);
    border-width: 4px;
}

h2{
    text-align: center;
    font-size:  50px;
    border-bottom: solid;
    border-color: rgb(148, 148, 143);
    text-shadow: rgb(8, 8, 8) 1px 0px 1px;
    color:var(--text-dark-color)
}

section p{
    padding: 2%;
    font-size: 25px;
}

aside{
    flex: 1;
    background-color: var(--bar-light-color);
    width: 14%;
    border-right: solid;
    border-color: var(--background-color-1);
    border-width: 4px;
    position: sticky;
}

aside nav{
    height: 100vh;
}

aside nav ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
}

aside nav ul li{
    flex: 4;
    text-decoration: none;
    background-color: var(--text-dark-color);
    padding: 15px;
    margin-top: 50px;
    margin-bottom: 50px;
    width: 100px;
    font-size: 30px;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    box-shadow: rgb(8, 8, 8) 1px 0 1px;
}

aside nav ul li:hover{
    opacity: 50%;
    color: var(--background-color-1);
}

aside nav ul li:visited{
    color: var(--text-dark-color-2);
    background: var(--background-color-1);
}

form{
    padding: 15px;
}

form label {
    display: block;
    width: 150px;
    font-weight: bold;
}

form input {
    display: block;
    width: 300px;
    height: 40px;
    font-size: 18px;
    float: left;
}

form textarea{
    display: block;
    resize: none;
}

form button{
    float: block;
    margin-top: 20px;
    font-size: 20px;
    font-weight: bold;
}

.projdef{
    flex: 1;
    display: flex;
    flex-direction: column;
    border-bottom: #dfe8e4;
}

.alignDiv{
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    font-size: 40px;
    color: var(--text-dark-color);
}

.alignDiv h3{
    font-size: 35;
    flex: 1;
}

.alignDiv a{
    flex: 1;
}
